<template>
  <div id="app">
    <!-- 容器 -->
    <div  id="cesium-viewer">
        <canvas
            v-show="showCanvas"
            id="qtcanvas"
            class="qt-canvas"
            ref="qtCanvas"
            @contextmenu.prevent
            contenteditable="true"
        ></canvas>
    </div>
    <div id="wrap">
      <el-button type="primary" @click="home">home</el-button>
      <el-button type="primary" @click="addOrDelTiles">{{!params.Tiles ? '销毁模型' : '加载模型'}}</el-button>
      <el-button type="primary" @click="addOrDelArcGisLayer">{{!params.ArcGisLayer ? '移除Arcgis影像' : '加载Arcgis影像'}}</el-button>
      <el-button type="primary" @click="addOrWorldImagery">{{!params.WorldLayer ? '移除天地图影像' : '加载天地图影像'}}</el-button>
      <el-button type="primary" @click="addOrWmtsImagery">{{!params.wmtsLayer ? '移除wmts影像' : '加载wmts影像'}}</el-button>
      <el-button type="primary" @click="addOrTemplateImagery">{{!params.TemplateLayer ? '移除遥感影像' : '加载遥感影像'}}</el-button>
      <el-button type="primary" @click="addOrDelTerrain">{{!params.Terrain ? '移除地形' : '加载地形'}}</el-button>
      <el-button type="primary" @click="addOrDelLabel">{{!params.Label ? '移除标签' : '加载标签'}}</el-button>
      <!-- <el-button type="primary" @click="addOrDelPolyline">{{!params.Polyline ? '移除线' : '加载线'}}</el-button>
      <el-button type="primary" @click="addOrDelPoint">{{!params.Points ? '移除点' : '加载点'}}</el-button>
      <el-button type="primary" @click="addOrDelPolygon">{{!params.Polygon ? '移除面' : '加载面'}}</el-button> -->
      <el-button type="primary" @click="addPoint">点击点对象</el-button>
      <el-button type="primary" @click="delPoint">移除点对象</el-button>
      <!-- <div id="laberDiv">左键选择，右键结束</div> -->
    </div>
    <div class="toolLeft">
      <!-- <el-el-button @click="add3dtiles">{{add3dtilesShow ? '清除' : '加载'}}深圳市三维Mesh模型(2021)</el-el-button> -->
      <el-button @click="switchEngine">切换{{engine === 'SSEngine' ? 'cesium' : '数生'}}引擎</el-button>
    </div>
  </div>
</template>

<script>
import qtloader from "../public/assets/qtloader"
import * as Cesium from 'cesium';
import { SSViewer,loadCesiumApi } from './package.js'
import "cesium/Build/Cesium/Widgets/widgets.css";
export default {
  name: 'App',
  data() {
    return {
      showCanvas: false,
      add3dtilesShow: false,
      engine: window.localStorage.getItem('engine'),
      tileset: null,
      params: {
        Tiles: true,
        ArcGisLayer: true,
        Terrain: true,
        WorldLayer: true,
        wmtsLayer: true,
        TemplateLayer: true,
        Label: true,
        Polyline: true,
        Points: true,
        Polygon: true,
      },
      dataSource: null
    }
  },
  created() {
    if(!window.localStorage.getItem('engine')) {
      // SSengine 数生引擎 Cesiumengine Cesium引擎  window.localStorage.getItem
      window.localStorage.setItem('engine','SSEngine')
    }
  },
  mounted() {
    if(this.engine === 'SSEngine') {
      window.initScene = () => {
          window.viewer = window.GlobalViewer;
          // this.addTdtImagery();
          // this.addImagery()
          // this.addwmtsImagery()
          loadCesiumApi()
          // this.home()
      };
      this.initMap();
      setTimeout(() => {
          // 深圳 114.054494,22.540745,1300
          // home 114.671438,27.710376,1000.00
          // this.positioning([ 114.054494,22.540745,1000.00])
          // this.$flyDestination({lon: 114.048532227874, lat: 22.491025537070062, height: 3161.550895400448,pitch:-30.591039086255005,heading:353.56261287254745})
      }, 2000);
      window.tilesetmodel = null
    }else {
      Cesium.Ion.defaultAccessToken = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiI1MDZlN2FlMC0zNzE4LTQ1YjgtYmQ2My1iMWE0MWIwMDViOTIiLCJpZCI6MjA4MDUxLCJpYXQiOjE3MTgzNTE3MzJ9.P-nlLB5GVoPEVx5A_efE4dYMBKzMFb1kqAMGzAya44Y'
      window.viewer = new Cesium.Viewer('cesium-viewer', {
        baseLayerPicker: false,
      })
      window.Cesium = Cesium
      loadCesiumApi()
      // this.addOrDelPolyline()
      // this.home()
      if (Cesium.FeatureDetection.supportsImageRenderingPixelated()) {
        //判断是否支持图像渲染像素化处理
        window.viewer.resolutionScale = window.devicePixelRatio
      }
      window.viewer.scene.postProcessStages.fxaa.enabled = true
      window.viewer.scene.debugShowFramesPerSecond = true // 显示帧率
    }
    
  },
  methods: {
    async initMap() {
        //初始化三维场景
        let qtCanvas = document.getElementById("qtcanvas"); //获取canvas标签
        this.$root.qtCanvas = qtCanvas;
        //加载引擎库
        // debugger
        let qtLoader = qtloader({
            canvasElements: [qtCanvas],
            path: "assets",
            showLoader: (loaderStatus) => {
                 this.showCanvas = false;
                 this.spinner = true;
                 this.statusMsg = loaderStatus + "...";
            },
            showError: (errorText) => {
                 this.spinner = true;
                 this.statusMsg = errorText;
                 this.showCanvas = false;
                 this.showError = true;
            },
            showExit: () => {
                 this.statusMsg = "Application exit";
                 if (qtLoader.exitCode !== undefined) {
                     this.statusMsg += " with code " + qtLoader.exitCode;
                 }
                 if (qtLoader.exitText !== undefined) {
                     this.statusMsg += " (" + qtLoader.exitText + ")";
                 }
                 this.spinner = true;
                 this.showCanvas = true;
            },
            showCanvas: () => {
                 this.spinner = false;
                 this.showCanvas = true;
                //  loadCesiumApi()
            },
       });
       //加载目录下命名包含有teramap的脚本
       qtLoader.loadEmscriptenModule("SSmap");
    },
    switchEngine() {
      // this.clearTilesetmodel()
      if(window.localStorage.getItem('engine')) {
         const result = window.localStorage.getItem('engine')
         window.localStorage.setItem('engine',result === 'SSEngine' ? 'cesiumEngine' : 'SSEngine')
         window.location.reload()
      }
    },
    home(){
      SSViewer.camera.flyTo({destination: [114.054494,22.540745, 1000]})
    },
    // 模型
    async addOrDelTiles(){
      if(this.params.Tiles) {
        const tileset = await SSCesium.Cesium3DTileset.fromUrl(new SSCesium.Resource({url: 'http://ss-sdk.oss-cn-shenzhen.aliyuncs.com/data/ft/tileset.json'}))
        window.tileset = tileset
        SSViewer.scene.primitives.add(tileset)
      }else {
        SSViewer.scene.primitives.remove(window.tileset)
      }
      this.params.Tiles = !this.params.Tiles
    },
    // Arcgis影像 cesium加载Arcgis影像报错
    async addOrDelArcGisLayer(){
      if(this.params.ArcGisLayer) {
        this.backattress('ArcGisLayer')
        window.nowLayer = new SSCesium.ImageryLayer(new SSCesium.WebMapTileServiceImageryProvider({
              url:
                "https://services.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer",
              layer: "Arcgis",
              style: "default",
              format: "image/jpeg",
              tileMatrixSetID: "default028mm",
              maximumLevel: 19,
              credit: "U. S. Geological Survey",
            }));
        SSViewer.imageryLayers.add(window.nowLayer);
      }else {
        SSViewer.imageryLayers.remove(window.nowLayer)
      }
      this.params.ArcGisLayer = !this.params.ArcGisLayer
    },
    // 天地图影像
    addOrWorldImagery(){ 
      if(this.params.WorldLayer) {
        this.backattress('WorldLayer')
        const url = this.engine === 'SSEngine' ? 'https://t1.tianditu.gov.cn/DataServer?T=img_c&x={x}&y={y}&l={z}&tk=789e558be762ff832392a0393fd8a4f1':'https://gibs.earthdata.nasa.gov/wmts/epsg4326/best/MODIS_Terra_CorrectedReflectance_TrueColor/default/{Time}/{TileMatrixSet}/{TileMatrix}/{TileRow}/{TileCol}.jpg'
        window.nowLayer = new SSCesium.ImageryLayer(new SSCesium.WebMapTileServiceImageryProvider({
              url,
              layer: "tianditu",
              style: "default",
              tileMatrixSetID: "250m",
              maximumLevel: 5,
              format: "image/jpeg",
              clock: window.viewer.clock,
              times: this.engine === "SSEngine" ? "" : Cesium.TimeIntervalCollection.fromIso8601({
                iso8601: "2015-07-30/2017-06-16/P1D",
                leadingInterval: true,
                trailingInterval: true,
                isStopIncluded: false, // We want stop time to be part of the trailing interval
                dataCallback: (interval, index) => {
                  let time;
                  if (index === 0) {
                    // leading
                    time = Cesium.JulianDate.toIso8601(interval.stop);
                  } else {
                    time = Cesium.JulianDate.toIso8601(interval.start);
                  }

                  return {
                    Time: time,
                  };
                },
              }),
              credit: "NASA Global Imagery Browse Services for EOSDIS",
            }));
        SSViewer.imageryLayers.add(window.nowLayer);
      }else {
        SSViewer.imageryLayers.remove(window.nowLayer)
      }
      this.params.WorldLayer = !this.params.WorldLayer
    },
    // wmts影像
    addOrWmtsImagery(){
      if(this.params.wmtsLayer) {
        this.backattress('wmtsLayer')
        const url = this.engine === 'SSEngine' ? 'http://t0.tianditu.gov.cn/cva_w/wmts?tk=cec5eeadc16a3c964451ff083266df0c' : 'http://t0.tianditu.gov.cn/cva_w/wmts?tk=cec5eeadc16a3c964451ff083266df0c'
        // 'http://t0.tianditu.gov.cn/cva_w/wmts?tk=cec5eeadc16a3c964451ff0a83266df0c'
        // layer: "cva",
        // style: "default",
        // tileMatrixSetID: "w",
        // format: "tiles",
        // maximumLevel: 18
        window.nowLayer = new SSCesium.ImageryLayer(new SSCesium.WebMapTileServiceImageryProvider({
              url,
              layer: "cva",
              style: "default",
              format: "tiles",
              tileMatrixSetID: "w",
              maximumLevel: 19,
              credit: "U. S. Geological Survey",
            }));
        SSViewer.imageryLayers.add(window.nowLayer);
      }else {
        SSViewer.imageryLayers.remove(window.nowLayer)
      }
      this.params.wmtsLayer = !this.params.wmtsLayer
    },   
    // 遥感影像
    addOrTemplateImagery(){
      if(this.params.TemplateLayer) {
        this.backattress('TemplateLayer')
        window.nowLayer = new SSCesium.ImageryLayer(new SSCesium.UrlTemplateImageryProvider({url: `${window.document.location.origin}/data/dom/ft/{z}/{x}/{y}.png`}))
        console.log(window.nowLayer);
        SSViewer.imageryLayers.add(window.nowLayer)
      }else {
        SSViewer.imageryLayers.remove(window.nowLayer)
      }
      this.params.TemplateLayer = !this.params.TemplateLayer
    },
    backattress(type) {
      const obj = {
        'ArcGisLayer':'ArcGisLayer','WorldLayer':'WorldLayer','wmtsLayer':'wmtsLayer','TemplateLayer':'TemplateLayer'
      }
      Object.values(this.params).some(it => !it) ? SSViewer.imageryLayers.remove(window.nowLayer) : ""
      Object.values(obj).forEach(it => {
        if(it !== type) {
          this.params[it] = true
        }
      })
    },
    // 地形
    async addOrDelTerrain(){
      if(this.params.Terrain) {
        const terrainProvider = await SSCesium.CesiumTerrainProvider.fromUrl(window.document.location.origin + "/" + "data/sz/")
        SSViewer.terrainProvider = terrainProvider
      }else {
        SSViewer.terrainProvider = new SSCesium.EllipsoidTerrainProvider() 
        // SSCesium.EllipsoidTerrainProvider({})
      }
      this.params.Terrain = !this.params.Terrain
    },
    // 标签图层
    addOrDelLabel(){
      if(this.params.Label) {
        if(this.engine === 'SSEngine') {
          window.nowgeoLabelLayer = SSCesium.dataSources.add({
            url: "data/geojson/labelLayer.geojson",
            fontColor: [255, 255, 255, 255],
            fontSize: 15,
            background: [0, 0, 0, 255],
            font: '微软雅黑',
          })
        }else {
          // 加载 GeoJSON 文件
          Cesium.GeoJsonDataSource.load('data/geojson/labelLayer.geojson').then((dataSource) => {
              window.viewer.dataSources.add(dataSource);
              window.nowgeoLabelLayer = dataSource
              // 获取数据源中的实体
              var entities = dataSource.entities.values;
  
              // 遍历实体并为每个实体添加标签
              for (var i = 0; i < entities.length; i++) {
                  var entity = entities[i];
                  // 添加标签
                  entity.label = {
                      text: entity.properties.ALIAS, // 使用实体的名称作为标签文本
                      fillColor: Cesium.Color.WHITE, // 设置文本颜色
                      font: '14px sans-serif', // 设置字体样式
                      outlineColor: Cesium.Color.TRANSPARENT, // 设置边框颜色
                      outlineWidth: 2, // 设置边框宽度
                      style: Cesium.LabelStyle.FILL_AND_OUTLINE, // 设置标签样式
                      showBackground: false,
                      distanceDisplayCondition: new Cesium.DistanceDisplayCondition(0, 1500)
                  };
              }
          }).catch(function(error){
              console.error(error);
          })
        }
      }else {
        SSViewer.dataSources.remove(window.nowgeoLabelLayer);
      }
      this.params.Label = !this.params.Label

    },
    // 线图层
    addOrDelPolyline(){
      // 深圳: https://geo.datav.aliyun.com/areas_v3/bound/440300_full.json
      // 东莞: https://geo.datav.aliyun.com/areas_v3/bound/441900.json
      // fetch('https://geo.datav.aliyun.com/areas_v3/bound/440300_full.json').then((response) => response.json())
      //   .then((json) => {
      //     console.log(json);
      //     json.features.forEach(it => {
      //       let positionData = []
      //       it.geometry.coordinates[0][0].forEach(itt => {
      //         positionData.push(Cesium.Cartesian3.fromDegrees(...itt,0))
      //       })
      //         window.viewer.entities.add({
      //           polyline: {
      //             positions: positionData,
      //             // clampToGround: true,
      //             width: 1,
      //           },
      //         }) 
      //         // 'oapoapl 13117904341'
      //     })
      //   })
      // // window.viewer.entities.add({
      // //   polyline: {
      // //     positions: positionData,
      // //     clampToGround: true,
      // //     width: 3,
      // //   },
      // // })
      // return
      if(this.params.Polyline) {
        const polylineLayer = SSCesium.EntityCollection({polylineLayer: {
          url: "data/geojson/road_polyline.geojson",
          color: [0,0,255,128],
          alpha: 0.5,
          width: 5.0,
          usedProperties: true,
          Altitude: 5.0,
        }})
        window.polylineLayer = polylineLayer
        SSViewer.scene.primitives.add({polylineLayer,type: 'polylineLayer'})
      }else {
        SSViewer.scene.primitives.remove({layer: window.polylineLayer,type: 'polylineLayer'})
      }
      this.params.Polyline = !this.params.Polyline
    },
    // 点图层
    addOrDelPoint(){
      if(this.params.Points) {
        const geoPointLayer = SSCesium.EntityCollection({geoPointLayer: {
          url: "data/geojson/ft-point.geojson",
          Imgurl: "images/circle.png",
          scale: 0.5,
          width: 40,
          height: 40,
        }})
        window.geoPointLayer = geoPointLayer
        SSViewer.scene.primitives.add({geoPointLayer,type: 'geoPointLayer'})
      }else {
        SSViewer.scene.primitives.remove({layer: window.geoPointLayer,type: 'geoPointLayer'})
      }
      this.params.Points = !this.params.Points
    },
    // 面图层
    addOrDelPolygon(){
      if(this.params.Polygon) {
        const geoPolygonLayer = SSCesium.EntityCollection({geoPolygonLayer: {
          url: "data/geojson/ft-polygon.geojson",
          color: [0, 183, 235, 128],
          pickcolor: [255, 0, 0, 128],
          alpha: 0.5,
          Imgurl: "images/circle.png",
          altitude: 1,
        }})
        window.geoPolygonLayer = geoPolygonLayer
        SSViewer.scene.primitives.add({geoPolygonLayer,type: 'geoPolygonLayer'})
      }else {
        SSViewer.scene.primitives.remove({layer: window.geoPolygonLayer,type: 'geoPolygonLayer'})
      }
      this.params.Polygon = !this.params.Polygon
    },
    // 点对象
    addPoint(){
      if(!window.BillboardList) window.BillboardList = []
      window.leftClickHandlerCallback = SSViewer.screenSpaceEventHandler.setInputAction(async (click) => {
        let earthPosition = undefined
        // 球面
        if (SSViewer.terrainProvider instanceof SSCesium.EllipsoidTerrainProvider) {
          earthPosition = SSViewer.scene.camera.pickEllipsoid(click.position);
          console.log('球面',earthPosition);
        }else {
          // 地形
          const ray = SSViewer.camera.getPickRay(click.position);
          earthPosition = SSViewer.scene.globe.pick(ray, window.viewer.scene);
          console.log('地形',earthPosition);
        }
        var pointEntity = SSViewer.entities.add({
            position: earthPosition,
            point: {
                pixelSize: 10,
                color: {red: 1,green: 0,blue: 0,alpha: 1},
                url: "images/circle.png", //路径
            }
        });
        window.BillboardList.push(pointEntity)
      },SSCesium.ScreenSpaceEventType.LEFT_CLICK)
      window.rightClickHandlerCallback = SSViewer.screenSpaceEventHandler.setInputAction((click) => {
        SSViewer.screenSpaceEventHandler.removeInputAction(SSCesium.ScreenSpaceEventType.LEFT_CLICK);
      },SSCesium.ScreenSpaceEventType.RIGHT_CLICK)
      
    },
    // 移除点对象
    delPoint(){
      if(!window.BillboardList) return
      const BillboardListLength = window.BillboardList.length
      for (let i = 0; i < BillboardListLength; i++) {
        SSViewer.entities.remove(window.BillboardList[i])
      }
      window.BillboardList = []
    },
    //鼠标点击事件
    mouseClickEvent(event) {
        if (viewer.scene == undefined) return;
        // //获取相机
        var camera = viewer.scene.mainCamera;
        var hit = new SSCesium.RaycastHit(); //射线投影
        //鼠标点击的位置，通过相机视角射线获取
        var ray = camera.screenPointToRay(event.x, event.y);
        var rayok = viewer.scene.raycast(ray, hit); //判断是否存在
        var point = 0;
        var tohic = null;
        if (rayok) {
            if (hit) {
                //获取坐标
                point = hit.point; //Vector3
                //Vector3转笛卡尔再转弧度
                tohic = point.toCartesian3().toCartographic();
            }
        }
        hit.delete();
        //鼠标左键点击
        if (event.button == 0) {
            const Billboard = SSViewer.entities.add({point: {
              position: point,
              url: "images/circle.png", //路径
              name: "wenzi",
              scale: 0.5, //比例
              altitude: 10, //海拔，非必填
              altitudeMethod: SSCesium.AltitudeMethod.Absolute,
            }})
            window.BillboardList.push(Billboard);
        } else if (event.button == 2) {
            this.drawcallbackevent();
        }
    },
    //鼠标移动事件
    mouseMoveEvent(event) {
        if (viewer.scene == undefined) return;
        //获取相机
        var camera = viewer.scene.mainCamera;

        var hit = new SSCesium.RaycastHit(); //射线投影
        //鼠标点击的位置，通过相机视角射线获取
        var ray = camera.screenPointToRay(event.x, event.y);
        var rayok = viewer.scene.raycast(ray, hit); //判断是否存在
        var point = 0;
        var tohic = 0;
        if (rayok) {
            if (hit) {
                //获取坐标
                point = hit.point; //Vector3
                //Vector3转笛卡尔再转弧度
                tohic = point.toCartesian3().toCartographic();
            }
        }
        hit.delete();
        var laberDiv 
        laberDiv = document.getElementById("laberDiv");
        laberDiv.style.display = "block";
        laberDiv.style.top = event.y + 10 + "px";
        laberDiv.style.left = event.x + 10 + "px";
        // document.getElementById("laberSpan").style.display = "block";
    },
    //结束绘制回调事件
    drawcallbackevent(){
        document
            .getElementById("cesium-viewer")
            .removeEventListener("mousedown", this.mouseClickEvent); //关闭鼠标点击事件
        document
            .getElementById("cesium-viewer")
            .removeEventListener("mousemove", this.mouseMoveEvent); //关闭鼠标移动事件
        document.getElementsByClassName("cesium-widget")[0].style.cursor = "default"; //鼠标箭头恢复
        var laberDiv = document.getElementById("laberDiv"); //关闭绘制提示
        laberDiv.style.display = "none";
    }
  },
}
</script>

<style lang="less">
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    height: 100%;
  }
  #cesium-viewer {
    position: absolute;
    width: 100vw;
    height: 100vh;
  }

  .qt-canvas {
    border: 0px none;
    background-color: white;
    height: 100%;
    width: 100%;
    outline: 0px solid transparent;
    caret-color: transparent;
    cursor: default;
    float: left;
  }
  #wrap {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
  }
  .toolLeft {
    // width: 50px;
    // height: 100vh;
    position: fixed;
    left: 100px;
    top: 200px;
    display: flex;
    flex-direction: row;
    .el-el-button {
      margin: 0 !important;
      margin-right: 10px;
    }
  }
</style>
