const myPlugin = {
  install(Vue) {
    Vue.prototype.$flyDestination = function({lon=0, lat=0, height=1000, duration=3, heading=0, pitch=-90, roll=0}) {
      let scene = window.viewer.scene;
      let camera = scene.mainCamera;
      let position = SSmap.Cartographic.fromDegrees(
        lon,
        lat,
        height
      ).toVector3();
      camera.cameraController().flyTo(position, duration, heading, pitch, roll );
    }
  }
}

export default myPlugin;